import React from "react";
import "./Circles.css";


const Circles = (props) => {
    return (
        <div className="Circles">
            <div className={props.selectBtnIdx === 0 ? "selected" : ""}>1</div>
            <div className={props.selectBtnIdx === 1 ? "selected" : ""}>2</div>
            <div className={props.selectBtnIdx === 2 ? "selected" : ""}>3</div>
            <div className={props.selectBtnIdx === 3 ? "selected" : ""}>4</div>
        </div>
    );

}
export default Circles;