import React, { Component } from 'react';
import CircleSelector from "./components/CircleSelector/CircleSelector";
import Circles from "./components/Circles/Circles";

import './App.css';


class App extends Component {
  state = {
    selectBtnIdx: 0,
  };

  handleButtonSelection = (e) => {
    let selectBtnIdx = parseInt(e.target.id);
    this.setState({ selectBtnIdx});
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">UNIT 4 FINAL ASSESSMENT</header>
        <main>
          <CircleSelector
          selectBtnIdx={this.state.selectBtnIdx}
          handleButtonSelection={this.handleButtonSelection}
          />
          <Circles selectBtnIdx={this.state.selectBtnIdx} />

        </main>
      </div>
    );
  }
}
          
        
export default App;
